//Базовый сброс

body {
  font-family: var(--primary-font);
  font-size: 12px;

  @media screen and (min-width: 700px) and (max-width: 1049px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1050px) {
    font-size: 16px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
}
